export default {
	data() {
		return {
			list: [],
			tabCur: 0,
			mainCur: 0,
			verticalNavTop: 0,
			load: true,
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar
		};
	},
	onLoad() {
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		let list = [{}];
		for (let i = 0; i < 26; i++) {
			list[i] = {};
			list[i].name = String.fromCharCode(65 + i);
			list[i].id = i;
		}
		this.list = list;
		this.listCur = list[0];
	},
	onShow() {
		//写请求数据的方法
	},
	onReady() {
		uni.hideLoading()
	},
	methods: {
		toForgetPwd() {
			/* 跳转忘记密码界面 */
			uni.navigateTo({
				url:'../login/forget_pwd'
			})
		},
		toSetting() {
			/* 跳转忘记密码界面 */
			uni.navigateTo({
				url:'../my/setting'
			})
		}
	}

}
